body {
  background-color: rgb(131, 128, 128);
}

.product img {
  width: 400px;
  height: 400px;
  border-bottom: solid 3px rgb(0, 0, 0);
}

.product {
  background-color: rgb(82, 181, 238);
  display: inline-block;
  border: solid 3px rgb(0, 0, 0);
  font-family: cursive;
  color: rgb(0, 0, 0);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  font-size: 15px;
}

.title {
  font-family:cursive ;
  font-size: 20px;
  flex: 1;
}

.text {
  padding: 10px;
}

.product-top {
  display: flex;
}

.shoppingcart {
  background-color: rgb(82, 181, 238);
  width: 341px;
  display: inline-block;
  border: solid 3px rgb(0, 0, 0);
  font-family: cursive;
  color: rgb(0, 0, 0);
  border-radius: 5px;
  font-size: 15px;
  padding: 30px;
}

.shoppingcart>button {
  width: auto;
  height: 35px;
  background-color: rgb(0, 174, 255);
  border-radius: 5px;
  border-color: rgb(46, 112, 236);
  margin-right: 1px;
  cursor: pointer;
  font-family: cursive;
  font-weight: bold;
}

.ordersummary {
  width: 40%;
  background-color: rgb(82, 181, 238);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: solid 3px rgb(0, 0, 0);
  font-family: cursive;
  color: rgb(0, 0, 0);
  border-radius: 5px;
  font-size: 15px;  
  padding: 20px;
}

.summary{
  width: 70%;
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  margin-bottom: 20px;
}

.form{
  background-color: rgb(82, 181, 238);
  display: flex;
  border: solid 3px rgb(0, 0, 0);
  font-family: cursive;
  color: rgb(0, 0, 0);
  border-radius: 5px;
  font-family: cursive;
  font-size: 15px;
  padding: 20px;
}

.formitems {
  width: 70%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

input[type=submit] {
  background-color:rgb(82, 181, 238);
  color:black;
  border-radius:5px;
  font-family: cursive;
  border: solid 2px rgb(32, 87, 189);
  margin-top: 15px;
  font-weight: bold;
}

.formparent {
  width: 100%;
  display: flex;
  justify-content: center;
}

.summaryparent{
  width: 100%;
  display: flex;
  justify-content: center;
}

.cartparent{
  width: 100%;
  display: flex;
  justify-content: center;
}

.productparent{
  width: 100%;
  display: flex;
  justify-content: center;
}

.carouselparent{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: cursive;
}

.ordersummary>button {
  width: 50%;
  height: 40px;
  background-color:rgb(82, 181, 238);
  color:black;
  border-radius:5px;
  font-family: cursive;
  border: solid 2px rgb(32, 87, 189);
  margin-top: 15px;
  font-weight: bold;
}

.carousel>button {
  width: 50%;
  background-color:rgb(82, 181, 238);
  border-right: solid 3px rgb(0, 0, 0);
  border-left: solid 3px rgb(0, 0, 0);
  border-bottom: solid 3px rgb(0, 0, 0);
  border-top: solid 1px rgb(0, 0, 0);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  color: black;
  margin-bottom: 5px;
  padding: 5px 20px; 
  font-size: x-large;
  cursor: pointer; 
  float: left; 
}

.btngroup>button{
  display: flex;
  justify-content: space-evenly;
  width: 100px;
  height: 30px;
  padding-bottom: 5px;
  color: white;
  background-color: rgb(0, 174, 255);
  border-radius: 5px;
  border-color: rgb(32, 87, 189);
  cursor: pointer;
  float: right;
  margin-bottom: 15px;
  margin-right: 5px;
  font-family: cursive;
  font-size: 15px;
}

#cartbutton button{
  display: inline-block;
  width: 25px;
  height: 25px;
  background-color: rgb(0, 174, 255);
  border-radius: 5px;
  border-color: rgb(46, 112, 236);
  margin: 1px;
  cursor: pointer;
}

input[type=text], input[type=number], input[type=email], input[type=tel] {
  border: solid 1px black;
  border-radius: 3px;
  height: 16px;
  margin-top: 3px;
  margin-bottom: 3px;
}

.cartitem img{
  width: 20%;
  border: solid 1px rgb(46, 112, 236);
  border-radius: 10px;
}

.itemname {
  width: 50px;
  position: relative;
}

.itemprice {
  width: 40px;
}

.itemamount {
  width: 50px;
}

.cartitem {
  display: flex;
  justify-content: space-between;
}

.ordersummary img {
    width: 20%;
    border: solid 1px rgb(46, 112, 236);
    border-radius: 10px;
}

.enkeksienää {
  display: flex;
  justify-content: left;
}

.juu {
  width: 70px;
}